:root {
  --section-overlay: rgba(0, 0, 0, 0.7);
  --background-overlay: rgba(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
}

.overlay {
  width: 100%;
  height: 100vh;
  background-color: var(--background-overlay);
}

.container {
  max-width: 800px;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.section {
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color: rgb(255, 255, 255);
}

.section_inputs, .section_temperature {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--section-overlay);
}

.section_inputs > input {
  border: 0.8px solid rgb(255, 255, 255);
  border-radius: 0.4rem;
  background-color: transparent;
  color: rgb(255, 255, 255);
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 200;
}

.section_inputs > button {
  padding: 10px 50px;
  border: none;
  border-radius: 0.4rem;
  font-size: 20px;
  font-weight: 500;
  background-color: rgb(255, 255, 255);
}

.section_inputs > button:hover {
  cursor: pointer;
  background-color: rgb(211, 211, 211);
}

.icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon > h3 {
  font-size: 15px;
  font-weight: 200;
  text-transform: capitalize;
}

.temperature > h1 {
  font-size: 60px;
}